.App {
  text-align: center;
  background-color: #f9f9f9;
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  margin: 0;
  border: none;
  overflow: auto;
  position: fixed;
}

/* Dairy cover  */

.App-name {
  font-size: 3.5em;
  font-family: "Open Sans Light";
  text-transform: uppercase;
  color: #ffffff;
  text-align: left;
}

.App-slogan {
  font-size: 1.7em;
  word-spacing: 0.1em;
  color: #808080;
  text-align: left;
}

.DairyCover {
  float: left;
  background-color: #2b2f3e;
  width: 20%;
  height: 100%;
  padding: 30px 0 0 40px;
  position: static;
}

/* Dairy content */

.App-title {
  font-size: 3.5em;
  font-family: "Open Sans Light";
  color: #565a69;
  text-align: left;
  margin: 0;
}

.DairyContent {
  float: right;
  margin: 0;
  border: 0;
  background-color: #f9f9f9;
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  padding: 40px 40px 40px 40px;
  -ms-flex-line-pack: justify;
      align-content: space-between;
  position: static;
}

/* task view */

.TaskListView {
  width: 45%;
  background-color: #ffffff;
  float: left;
  -ms-flex-line-pack: center;
      align-content: center;
  padding: 10px 20px 0 30px;
  -webkit-box-shadow: -1px 1px 10px #bababa;
          box-shadow: -1px 1px 10px #bababa;
}

.AddTaskBar {
  padding: 20px 10px 0px 20px;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.AddTaskBar input[type="submit"] {
  width: 30%;
  height: 2.8m;
  margin-left: 2%;
  font-size: 1em;
  line-height: 2.75em;
}

.AddTaskBar input[type="text"] {
  font-family: "Open Sans Semibold";
  font-size: 1.5em;
  height: 2.1em;
  line-height: 2.75em;
  border: 2px #cccccc solid;
  border-radius: 5px;
  background-color: inherit;
  margin: 0;
  padding: 0 0 0 0.707em;
  vertical-align: middle;
  width: 60%;
}

::-webkit-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #b4b4b4;
  font-size: 1em;
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #b4b4b4;
  font-size: 1em;
  opacity: 1; /* Firefox */
}

::-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #b4b4b4;
  font-size: 1em;
  opacity: 1; /* Firefox */
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #b4b4b4;
  font-size: 1em;
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #b4b4b4;
}

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: #b4b4b4;
}

.TaskListView nav {
  margin: 0;
  padding-top: 10px;
}

.TaskList {
  padding: 0px 10px 0px 20px;
  min-height: 10px;
  max-height: 650px;
  overflow: hidden;
  overflow-y: auto;
}

.TaskRecord {
  border: none;
  border-bottom: 2px solid #e8e8e8;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 15px 0;
  overflow: auto;
}

.TaskRecord:first-of-type {
  padding-top: 5px;
}

.TaskRecord:last-of-type {
  padding-bottom: 10px;
}

.task-selected .indicator {
  visibility: visible;
}

.indicator {
  position: absolute;
  width: 8px;
  height: 84px;
  margin-left: -54px;
  background-color: #f72d57;
  visibility: hidden;
}

.task {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  vertical-align: middle;
}

.task-name {
  font-family: "Open Sans Semibold";
  text-align: left;
  font-size: 1.5em;
  line-height: 1.5em;
}

input[type="button"],
input[type="submit"] {
  background-color: #27ccc0;
  border: 1px solid #27ccc0;
  color: #ffffff;
  border-radius: 5px;
  text-align: center;
  margin: 0;
}

.AddTaskBar input[type="submit"] {
  font-family: "Open Sans Semibold";
  font-size: 1.5em;
  height: 2.25em;
  padding: 0;
  line-height: 1.7em;
}

input[type="button"].num-comments {
  font: 700 1.3em/1.3em "Open Sans";
  border-radius: 15px;
  text-align: center;
  height: 1.3em;
  float: left;
  margin: 0 0.5em;
  padding: 0 10px 0 10px;
}

input[type="button"].delete-btn {
  font-family: "Open Sans Light";
  font-size: 1.5em;
  background-color: #ffffff;
  color: #ff305a;
  width: 6.2em;
  margin: 0;
  padding: 0;
  height: 2.25em;
  border: 2px solid #ff3059c0;
}

/* Comment view */

.CommentListView {
  width: 55%;
  -ms-flex-line-pack: center;
      align-content: center;
  background-color: #ffffff;
  padding: 10px 40px 10px 30px;
  margin: 0;
  margin-left: 2%;
  -webkit-box-shadow: -1px 1px 10px #bababa;
          box-shadow: -1px 1px 10px #bababa;
  overflow: auto;
}

.CommentListView nav {
  padding: 0;
  margin: 0;
}

.CommentList {
  width: 100%;
  min-height: 10px;
  max-height: 600px;
  padding-top: 15px;
  margin-left: -40px;
  overflow: hidden;
  overflow-y: auto;
}

.CommentRecord {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-item-align: auto;
      align-self: auto;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-line-pack: justify;
