.CommentRecord:last-of-type {
  border-bottom: none;
  padding-bottom: 10px;
}

.comment-name {
  font: 300 1.14em/1.5em "Open Sans";
  margin-left: 25px;
  margin-top: -5px;
  display: block;
  width: 100%;
  text-align: left;
  color: #666666;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
  white-space: pre-line;
}

.placeholder-icon {
  width: 81px;
  height: 81px;
  background-color: #e6e6e6;
}

.CommentRecord:nth-child(odd) .placeholder-icon {
  background-color: #ff8a00;
}

.CommentRecord:nth-child(even) .placeholder-icon {
  background-color: #47568c;
}

.AddCommentBar {
  display: -ms-flexbox;
  display: flex;
  height: 100px;
  padding-right: 40px;
  padding-bottom: 30px;
}

.comment-new,
.comment-new textarea {
  width: 100%;
  height: 100%;
}

.comment-new textarea:disabled {
  background-color: #eeeeee;
}

.comment-new textarea {
  border: 2px solid #cccccc;
  font: 300 1.14em/1.5em "Open Sans";
  text-align: left;
  color: #666666;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
  padding-left: 0.5em;
  padding-top: 0.2em;
  resize: none;
  margin-left: 25px;
  white-space: pre-wrap;
}


